/* Scss Document */
/* Common CSS */
img {
	width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}
p {
	line-height: 2;
}
.of_hidden {
	overflow: hidden;
}
.m_0_auto {
	margin: 0 auto;
}
.wip {
	background: black;
	opacity: 1;
}
.conts_align_c {
	padding-left: 0rem;
	padding-right: 0rem;
}
.pad_none {
	padding: 0;
	img {
		width: 100%;
	}
}
.no_list_icon {
	list-style: none;
}
.btn-circle {
	width: 70px;
    height: 70px;
    text-align: center;
    padding-left: 25px;
    font-size: 14px;
    line-height: 4.2;
    border-radius: 50px;
    border: solid 1px #969696;
    color: #969696;
	text-shadow: 1px 1px white, -1px -1px white;
    font-weight: 100;
	transition : border 500ms ease-out; 
	-webkit-transition : border 500ms ease-out; 
	-moz-transition : border 500ms ease-out;
	-o-transition : border 500ms ease-out;
	transition : color 500ms ease-out; 
	-webkit-transition : color 500ms ease-out; 
	-moz-transition : color 500ms ease-out;
	-o-transition : color 500ms ease-out;
	&:hover, &:active, &:focus {
		color: #74BCA3;
		border: solid 1px #74BCA3;
	}
}


/* Font-families */
@import url('https://fonts.googleapis.com/css?family=Merriweather:300i,400i&display=swap');

.merriweather {
	font-family: 'Merriweather', serif;
	font-weight: 300;
    font-style: italic;
}
/* Font-sizes */
.fSize_8 {
	font-size: 0.8rem;
}
.fSize_9 {
	font-size: 0.9rem;
}
.fSize1 {
	font-size: 1rem;
}
.fSize1_5 {
	font-size: 1.5rem;
}
.fSize2 {
	font-size: 2rem;
}
.fSize5 {
	font-size: 5rem;
}

/* Font-color */
.mellow_green {
	color: #74BCA3;
}
.gray {
	color: #AAA;
}

/* Button */
.btn_mellow_green {
	background: #FFF;
	border-color: #AAA;
	
	&:hover, &:active, &:focus {
		color: #FFF!important;
		background: #74BCA3;
		border-color: #74BCA3;
    }
	& a:hover, & a:active, & a:focus {
		color: #FFF;
		background: #74BCA3;
		border-color: #74BCA3;
    }
}


@keyframes huerotator {
	0% {
		-webkit-filter: hue-rotate(0deg);
		filter: hue-rotate(0deg);
	}
	100% {
		-webkit-filter: hue-rotate(360deg);
		filter: hue-rotate(360deg);
	}
}

body {
	font-weight: 100;
	.row {
		margin: 0;
	}
	h2 {
		font-weight: 800 ;
	}
	/* gNav */
	#gNav {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;
		height: 100%;
		min-height: 100vh;
		h1 a {
			display: block;
			width: 60%;
			margin: 0 auto;
			background: url("../images/logo_ttl_01.png") center no-repeat;
			background-size: contain;
		}
		nav {
			z-index: 1;
		}
		.navbar-toggler {
			margin: 1rem auto;
			&:focus {
				outline: none;
			}
		}
		#NavbarWrap {
			z-index: 2;
			.card {
				width: 12rem;
				margin-left: 1.5rem;
				background: none;
				&:before {
					content: "";
					position: absolute;
					width: 0;
					height: 0;
					border: 10px solid transparent;
					right: 100%;
					bottom: 100%;
					margin-right: -60px;
					border-top-width: 0;
					border-bottom-color: rgba(0,0,0,.2);
				}
				&:after {
					content: "";
					position: absolute;
					width: 0;
					height: 0;
					border: 9px solid transparent;
					right: 100%;
					bottom: 100%;
					margin-right: -59px;
					border-top-width: 0;
					border-bottom-color: #FFF;
				}
				.list-group-item {
					border: none;
					&:nth-child(1) a {
						&:focus, &:hover {
							color: #F02F1E;
						}
					}
					&:nth-child(2) a {
						&:focus, &:hover {
							color: #50240B;
						}
					}
					&:nth-child(3) a {
						&:focus, &:hover {
							color: #74BCA3;
						}
					}
					&:nth-child(4) a {
						&:focus, &:hover {
							color: #EBCE56;
						}
					}
					&:nth-child(5) a {
						&:focus, &:hover {
							color: #6B764E;
						}
					}
					.dropdown-item {
						padding: .75rem 1.25rem;
						transition : color 500ms ease-out; 
						-webkit-transition : color 500ms ease-out; 
						-moz-transition : color 500ms ease-out;
						-o-transition : color 500ms ease-out;
						&:focus, &:hover {
							text-decoration: none;
							background-color: #FFF;
						}
					}
				}
			}
			.navbar-collapse.collapse.in, .open>.dropdown-menu {
				display: inline-block;
			}
			
		}
		#slide_counter {
			position: absolute;
			width: 100%;
			top: 20rem;
			left: 0;
			transform: rotate(-90deg);
			white-space: nowrap;
			margin-top: 10rem;

			progress[value] {
				-webkit-appearance: none;
				appearance: none;

				width: 15em;
				height: 5px;
			}
			progress[value]::-webkit-progress-bar {
				background-color: #EEE;
				border: none;
				border-radius: 5px;
				box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
			}
			progress[value]::-webkit-progress-value {
				background-color: #74BCA3;
			}
			label {
				padding-left: 1rem;
				letter-spacing: 0.1rem;
			}
		}
		#Navbar_bottom {
			padding-bottom: 5rem;
			position: absolute;
			width: 100%;
			top: 45rem;
			#btnSNS {
				a {
					width: 1rem;
					height: 1rem;
					margin: 0 auto;
				}
				li {
					&:nth-child(1) a {
						background: url("../images/ico_sns_fb.png") center no-repeat;
						background-size: cover;
					}
					&:nth-child(2) a {
						background: url("../images/ico_sns_ig.png") center no-repeat;
						background-size: cover;
					}
				}
			}
		}
	}

	/* #conts */
	#conts {
		.ttl_label {
			overflow: hidden;
			h2 {
				font-weight: 100;
				font-size: 1rem;
			}
		}
		/* #work */
		#work { 
