						height: 20px;
						position: relative;
						a {
							color: black;
							z-index: 11;
						}
					}
				}
			}
			/* Carousel */
			#carouselExampleInterval {
				.carousel-item {
					min-height: 100vh;
					img {
						max-height: 78%;
						position: absolute;
						bottom: 0;
						right: 0;
					}
					.carousel-caption {
						text-align: left;
						position: absolute;
						top: 5rem;
						left: 15rem;
						h3 {
							font-weight: 800;
							font-size: 5rem;
							color: #000;
						}
						h4 {
							font-size: 1rem;
							font-weight: 300;
						}
						a.btn-circle {
							margin-top: 15rem;
							margin-left: 10rem;
						}
					}
				}
			}
			#arrow_sp {
				display:  none;
			}
		}
		#contents_elements {
			.ttl_label {
				/*padding-bottom: 1rem;*/
			}
		}
		.container {
			padding-top: 5rem;
			padding-left: 155px;
			.conts_groop {
				padding-bottom: 15rem;
				.row {
					.col-md-4 {
						padding-left: 0;
						padding-right: 0;
					}
					h3 {
						z-index: 1;
						position: relative;
						
						white-space: nowrap;
						font-weight: 800;
						font-size: 5rem;
					}
					h4 {
						position: relative;
						top: 3rem;
						font-weight: 100;
						font-size: 1rem;
