				}
				.conts_texts {
					padding-top: 5rem;
					padding-bottom: 5rem;
					line-height: 2;
				}
			}
			
			/* about_us */
			#about_us {
				#servies_list {
					position: relative;
					top: 0rem;
					list-style: none;
					font-weight: 800;
					font-size: 4rem;
