						}
						&:nth-child(2) {
							color: #50240B;
						}
						&:nth-child(3) {
							color: #74BCA3;
							margin-left: 13rem;
						}
						&:nth-child(4) {
							color: #EBCE56;
							margin-left: 15rem;
						}
						&:nth-child(5) {
							color: #6B764E;
							margin-left: 10rem;
						}
					}
				}
			}
			/* #graphic_design */
			#graphic_design.conts_groop .row {
				ul {
					position: absolute;
					top: 28rem;
					li:last-child {
						padding-top: 2rem;
					}
				}
				h3 {
					padding: 3rem;
					span {
