			}
			/* #advertising */
			#advertising.conts_groop .row {
				h3 {
					padding-right:  5rem;
					padding-bottom: 2rem;
					text-align: right;
				}
				.conts_texts {
					padding-left: 3rem;
					padding-right: 3rem;
				}
			}
			/* #page-top */
			#page-top {
				position: fixed;
				bottom: 60px;
				right: 20px;
				font-size: 80%;
				a {
					background: rgba(0, 0, 0, 0.3);
					text-decoration: none;
					color: #fff;
					width: 100px;
					padding: 25px 0;
					text-align: center;
					display: block;
					border-radius: 10px;
					&:hover {
						text-decoration: none;
						background: rgba(0, 0, 0, 0.6);
					}
				}
			}
		}
		/* #testimonials */
		#testimonials {
			width: 100vw;
			height: auto;
			padding-top: 5rem;
			padding-bottom: 5rem;
			background: url("../images/txt_quotes.png") no-repeat 23% 33% #F6F6F6;
			.row {
				h3 {
					font-size: 5rem;
					font-weight: 800;
				}
				.under_line {
					border: solid 1px #74BCA3;
					width: 30px;
					height: 2px;
					margin:  1rem auto 2rem;
				}
				h4 {
					color: #AAAAAA;
					padding-bottom: 2rem;
					font-family: 'Merriweather', serif;
					font-weight: 400;
					font-style: italic;
				}
				p {
					padding-right: 10rem;
					padding-left: 10rem;
				}
				#testimonials_thumbnail {
					width: 28rem;
					list-style: none;
					li {
						width: 90px;
						height: 90px;
						float: left;
					}
				}
				.testimonials_on {
					position: relative;
					top: -1.5rem;
					filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.3));
				}
			}
		}
		/* footer */
		footer {
			background: #000;
			width: 100vw;
			height: auto;
			min-height: 100vh;
			padding-top: 10rem;
			h2 {
				font-size: 9rem;
			}
			#contact_form {
				padding-left: 10rem;
				button {
					display: block;
					margin: 0 0 0 auto;
				}
			}
		}
	}

}

@media (min-width: 1600px) {
